.main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
    margin-top: 4rem;
  }
  
  .greet-main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
    margin-top: 2rem;
  }
  
  .greeting-main {
    display: flex;
  }
  
  .subTitle {
    /* font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
    color: #868e96 !important;
  }

  .text-left {
    text-align: left;
  }
  
  .greeting-main {
    display: flex;
  }
  
  .greeting-main > * {
    flex: 1;
    margin-bottom: 30px;
  }
  
  .empty-div {
    margin-bottom: 40px;
  }
  
  .button-greeting-div {
    display: flex;
    margin-top: 40px;
  }
  
  .download-link-button {
    text-decoration: none;
  }

  .greeting-java {
    font-family: 'Courier New', Courier, monospace;
    font-size: 40px;
    line-height: 1;
    color: #151313 !important;
  }
  
  .greeting-text {
    font-size: 70px;
    line-height: 1.1;
    font-weight: 500;
    color: #000000 !important;
  }
  
  .greeting-text-p {
    font-size: 30px;
    line-height: 40px;
  }
  
  /* .greeting-image-div > img {
    max-width: 100%; 
    width: auto;      
    height: auto;
  } */
  
  .wave-emoji {
    animation-duration: 1.8s;
    animation-iteration-count: infinite;
    animation-name: wave;
    display: inline-block;
    transform-origin: 70% 70%;
  }
  
  @keyframes wave {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(-10deg);
    }
    20% {
      transform: rotate(12deg);
    }
    30% {
      transform: rotate(-10deg);
    }
    40% {
      transform: rotate(9deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  /* Media Query */
  @media (max-width: 1380px) {
    .greeting-text {
      font-size: 50px;
    }
  
    .greeting-text-p {
      font-size: 20px;
    }
  }
  
  @media (max-width: 768px) {
    .button-greeting-div {
      justify-content: space-around;
    }
  
    .greeting-text {
      font-size: 30px;
      text-align: center;
    }
  
    .greeting-text-p {
      font-size: 16px;
      line-height: normal;
      text-align: center;
    }
  
    .greeting-main {
      display: block;
    }
  
    .greeting-image-div {
      height: 56px;
    }
  }
  
  @media (max-width: 320px) {
    .main {
      width: auto;
    }
  }

  